<template>
  <div class="jl-information jl-onlinebill">
    <div class="jdy-content jdy-transfer fleft jl-ad">
      <div class="jdy-content-inner-trip">
        <div class="jdy-tab">
          <a href="javascript:;" title="" @click="jdytab(null)" :class="{ active: jdytabActive==null }">
            <span></span>
          </a>
          <a href="javascript:;" title="" @click="jdytab(3)" :class="{ active: jdytabActive==3 }">
            <span></span>
          </a>
          <a href="javascript:;" title="" @click="jdytab(4)" :class="{ active: jdytabActive==4 }">
            <span></span>
          </a>
          <el-button type="default" class="fr mt10 mr10 btnInTab" @click="refreshBtn">
            
          </el-button>                             
        </div>
        <div class="jdy-searchPart">
          <el-form :model="ruleForm" :inline="true" ref="ruleForm" label-width="100px">
              <el-row style="width: 100%;">
                  <el-form-item>
                    <el-select v-model="ruleForm.admissionType" clearable placeholder="" style="width: 100%;">
                      <el-option :key="value.id" v-for="value in ticketList" :label="value.tName" :value="value.id">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label="" label-width="100px">
                    <el-input placeholder="" v-model.trim="ruleForm.searchString" class=""></el-input>
                  </el-form-item>
                  <el-button type="primary" @click="firstSearch" class="btnInTab"></el-button>
              </el-row>
          </el-form>
        </div>
      </div>
      <div class="jdy-table p10 jl-onlinebill">
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column label="" type="index">
          </el-table-column>
          <el-table-column label="" min-width="150px">
              <template scope="scope">
                  <span class="jl-noticeTitle">{{scope.row.ticketProductCode}}</span>
              </template>
          </el-table-column>
          <el-table-column label="" min-width="350px">
            <template scope="scope">
              <div style="text-align:left;padding:10px 0 10px 30px;">
              <span>{{scope.row.ticketProductName}}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span>{{scope.row.ticketProductType==0?"":""}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.productManagerName}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px"> 
            <template scope="scope">
              <span>{{scope.row.admissionStatus | filterStatus}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.createrTime| dateFormat}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="180">
            <template scope="scope">
              <el-button type="primary" @click="upTicket(scope.row.id)" size="mini" v-if="scope.row.admissionStatus == 3"></el-button>
              <el-button type="primary" @click="offTicket(scope.row.id)" size="mini" v-if="scope.row.admissionStatus == 4"></el-button>
                <!-- <el-button type="primary" @click="consumeDeatil(scope.row.id)" size="mini"></el-button> -->
              <el-button type="primary" @click="stockDetail(scope.row.id)" size="mini"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="clearfix">
          <el-pagination class="fright pageMargin" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSizeAll" :total="tableDataTotal">
          </el-pagination>
        </div>
      </div>
    </div>
    <!--   begin-->
    <jdy-alert title="" @closeAlert="closeAlert" v-if="consume.flag" class="alertCityList showtfcity refund" style="width:1000px;">
      <div class="produceWrap">
        <el-form :model="consume" ref="consume" style="margin-top:10px;">
          <el-row>
              <el-col :span="20">
                  <el-form-item>
                      <el-date-picker v-model="usefulDate" type="daterange" placeholder="" @change="dateRangeChange"></el-date-picker>
                  </el-form-item>
                  <el-button type="primary" @click="searchProduce()" size="small"></el-button>
              </el-col>
          </el-row>
        </el-form>
        <el-table :data="produceData" border show-summary class="all produceList">
          <el-table-column label="" min-width="250px">
            <template scope="scope">
              <div style="text-align:left;padding:10px 0 10px 30px;">
              <span>{{scope.row.ticketProductCode}}{{scope.row.ticketProductName}}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="100px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.productManagerName}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="produceNum" min-width="100px" sortable>
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.produceNum}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="100px">
            <template scope="scope">
              <span class="jl-noticeTitle" v-if="scope.row.pStatus == 0"></span>
              <span class="jl-noticeTitle" v-if="scope.row.pStatus == 1"></span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.createrTime | dateFormat}}</span>
            </template>
          </el-table-column>
        </el-table>
        <!--    begin-->
        <div class="clearfix">
          <el-pagination class="fright pageMargin" @current-change="produceHandleCurrentChange" :current-page.sync="produceCurrentPage" :page-size="producePageSizeAll" :total="stockTableDataTotal">
          </el-pagination>
        </div>
        <!-- end-->
      </div>
    </jdy-alert>
    <!--   end--> 
    <!--   begin-->
    <jdy-alert title="" @closeAlert="closeAlert" v-if="stock.flag" class="alertCityList showtfcity refund" style="width:1000px;">
      <div class="produceWrap">
        <el-table :data="stockTableData" border style="width: 100%">
          <el-table-column label="" min-width="120px">
              <template scope="scope">
                <span>{{scope.row.apName}}</span>
              </template>
          </el-table-column>
          <el-table-column label="" min-width="80px">
            <template scope="scope">
              {{scope.row.apType == 0?"":""}}     
            </template>
          </el-table-column>
          <el-table-column label="" > 
            <template scope="scope">
              <span>{{scope.row.apStock==-1?"":scope.row.apStock}}</span>
            </template>
          </el-table-column>  
          <el-table-column label="" min-width="200px">
            <template scope="scope">
              {{scope.row.lifeStartDate}}~{{scope.row.lifeEndDate}}
            </template>
          </el-table-column>                  
          <el-table-column label="" min-width="200px">
            <template scope="scope">
              {{scope.row.updateTime | dateFormat('time')}}
            </template>
          </el-table-column>
        </el-table>
        <!--    begin-->
        <div class="clearfix">
          <el-pagination class="fright pageMargin" @current-change="stockCurrentChange" :current-page.sync="stockcurrentPage" :page-size="stockpageSizeAll" :total="stockTableDataTotal">
          </el-pagination>
        </div>
        <!-- end-->
      </div>
    </jdy-alert>
    <!--   end-->         
  </div>
</template>

<script>
import jdyAlert from "@/components/Alert";
export default {
  components: {
    jdyAlert
  },
  name: "information",
  data() {
    return {
      currentPage:1,
      produceCurrentPage:1,
      pageSizeAll:20,
      tableData: [],

      producePageSizeAll:20,
      tableDataTotal:0,
      
      refundFlag:false,
      
      // 
      stockcurrentPage:1,
      stockpageSizeAll:20, 
      stockTableDataTotal:0,
      stocktableParam:{
        "currPage": 1,
        "pageSize": 20,
        "pid": this.$route.query.id       
      },             
      stockTableData:[],
      // 
      consumecurrentPage:1,
      consumeproduceCurrentPage:1,
      consumepageSizeAll:20,  
      consumeTableDataTotal:0,     
      consumeTableData:[],

      produceData:[],
      fullscreenLoading: false, //loadding
      jdytabActive: null,
      usefulDate:'',
      admissionBaseId:'',
      ruleForm: {
        admissionType:null,
        status:[3,4],// 0: 1: ,
        searchString: '',
        pageSize: 20,
        pageIndex: 1,
      },
      consume: {
        flag:false,
        admissionBaseId:'',
        searchEndDate:'',
        searchStartDate:'',
      },
      stock: {
        flag:false,
        admissionBaseId:'',
        searchEndDate:'',
        searchStartDate:'',
      },      
      //
      ticketList:[
        {
          id: 1,
          tName: ""
        },
        {
          id: 0,
          tName: ""
        }
      ],
    
    };
  },
  mounted() {
    this.getTableData();
  },
  filters:{
    filterStatus(value) {
      var status="";
      switch(value){
          case 0:
            status = "";
            break;
          case 1:
            status = "";
            break;
          case 2:
            status = "";
            break;
          case 3:
            status = "";
            break; 
          case 4:
            status = "";
            break;
          case -1:
            status = "";
            break;                       
      }
      return status
    }
  },
  methods: {
    getTableData() {
      this.$http.post(api_prefix + "admission/base/list", this.ruleForm).then(response => {
          let data = response.body.body;
          if (response.body.code == 0){
            this.tableData = data.list;
            this.tableDataTotal = data.total;          
          }else{
            this.$alert(data.message)
          }     
        },
        response => {
          console.log("");
        }
      );
    },
    consumeDeatil(id){
      this.consume.flag=true;
      this.$nextTick(function() {
        jdyFn.setAlertStyle("showtfcity");
      });
    },
    stockDetail(id){
      this.stock.flag=true;
      this.$nextTick(function() {
        jdyFn.setAlertStyle("showtfcity");
      });
      this.stocktableParam.pid=id;
      this.getStockTableData();
    },
    getStockTableData(){
        this.$http.post(api_prefix + 'admission/price-list',this.stocktableParam).then(response => {
          if (response.data.code == 0) {
            this.stockTableData = response.data.body.resultList;
            this.stockTableDataTotal=response.data.body.totalNum
          } else {
            this.$alert(response.data.message)
          }
        }, response => {
          console.log('');
        }); 
    },
    stockCurrentChange(val){
      this.stocktableParam.currPage = val;
      this.getStockTableData()
    },
    getProduceData(val) {
      this.consume.admissionBaseId = val;
      //table
      this.$http
        .post(api_prefix + "/admission/produce/list", this.consume)
        .then(
          response => {
            let data = response.body.body;
            this.produceData = data;
            this.stockTableDataTotal = data.total;
          },
          response => {
            console.log("");
          }
        );
    },
    jdytab(index) {
      this.jdytabActive = index;
      if(index == null){
        this.ruleForm.status = [3,4];
      }else{
        this.ruleForm.status = [index];
      }
      this.getTableData();
    },
    //currentPage 
    handleCurrentChange(val) {
      console.log(val, 'val')
      this.ruleForm.currPage = val;
      this.getTableData()
    },
    produceHandleCurrentChange(val){
      this.consume.currPage = val;
      this.getProduceData()
    },
    firstSearch(){
        this.getTableData();
    },
    //
    refreshBtn() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
        this.$message({
          showClose: true,
          message: "",
          type: "success"
        });
        this.getTableData();
      }, 1000);
    },
    dropoff(id){
      this.refundFlag = true;
      this.getProduceData(id)
      this.admissionBaseId = id;
      this.$nextTick(function() {
        jdyFn.setAlertStyle("showtfcity");
      });
    },
    searchProduce(){
      this.getProduceData(this.admissionBaseId)
    },
    dateRangeChange(value){
      if (value) {
          let values = value.split(" - ");
          this.consume.searchStartDate = values[0];
          this.consume.searchEndDate = values[1];
      }
    },
    closeAlert() {
      this.stock.flag=false;
      this.consume.flag=false;
      $(".alertbgg").remove();  
    },
    openStock(){
      this.stock.flag=true;
      this.$nextTick(function() {
        jdyFn.setAlertStyle("showtfcity");
      });
    },
    openConsume(){
      this.consume.flag=true;
      this.$nextTick(function() {
        jdyFn.setAlertStyle("showtfcity");
      });
    },
    upTicket(id){
      this.$http.get(api_prefix + 'admission/base/startSales/' + id).then(response => {
          if (response.data.code == 0) {
              this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
            this.getTableData();
          } else {
            this.$alert(response.data.message)
          }
        }, response => {
          console.log('');
        }); 
    },
    offTicket(id){
      this.$http.get(api_prefix + 'admission/base/stopSales/' + id).then(response => {
          if (response.data.code == 0) {
              this.$message({
              showClose: true,
              message: "",
              type: "success"
            });
            this.getTableData();
          } else {
            this.$alert(response.data.message)
          }
        }, response => {
          console.log('');
        });
    }
  }
};
</script>
<style scoped>
.gz-itemMragin {
  margin-top: 10px;
}

.gz-Bg {
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  z-index: 99;
}

.alertAddProduct {
  left: 0px;
  right: 0px;
  margin: auto;
}

.gz-smallWidth {
  width: 122px;
}

.gz-largeWidth {
  width: 300px;
}

.jdy-content {
  min-height: initial;
  box-sizing: border-box;
}
.orderout{
  margin: 10px -10px 0 0;
}
.produceWrap{
  padding: 10px;
}
.produceList{
  height: 400px;
  overflow-y: scroll;
}
</style>




