<template>
    <el-row class="jdy-content-inner-trip pt10 eltabs">
    <el-tabs v-model="activeNameInner" @tab-click="handleClick">
        <el-tab-pane label="" name="ticketPolicyTicketInfo"></el-tab-pane>
        <el-tab-pane label="" name="ticketPolicyPriceInfo"></el-tab-pane>
        <el-tab-pane label="" name="ticketPolicyContract"></el-tab-pane>
        <!-- <el-tab-pane label="" name="ticketPolicyExpandInfo"></el-tab-pane> -->
    </el-tabs>  
    <el-button class="eltabbtn btnInTab" @click="goback"></el-button>   
    <el-button class="eltabbtnsave btnInTab" type="primary" @click="savedata" v-show="saveBtnInner">{{title}}</el-button>
    </el-row>
</template>
<script>
  import api_prefix from '@/assets/js/apiUrl'

  export default {
    data(){
        return {
        //   
        saveBtnInner:this.savebtn,
        activeNameInner:this.activename,
        testflag:false
        }
    },
    props:{
        activename:String,
        savebtn:Boolean,
        title:{
            type: String,
            default: ""            
        }
    },      
    methods:{
        /**
         * @description 
         * @todo 
         * 1.tab
         * 2.active
         * 3.
         * 4.
        */
        handleClick(value){
            this.$router.push({ name: value.name,query:{id:this.$route.query.id}})          
        },
        savedata(){
            this.$emit('savedata');
        },
        goback(){
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$router.push({ name: "ticketPolicyList"})
            }).catch(() => {

            });
        },
    }
  }
</script>

<style scope="scope">
.eltabs{
  position: relative;
}
.eltabbtn{
  position: absolute;
  right: 0;
  bottom: 25px;
}
.eltabbtnsave{
  position: absolute;
  right: 100px;
  bottom: 25px;  
}
</style>



