<template>
  <div class="jdy-content jdy-transfer fleft">

    <div class="jdy-content-inner">
      <inner-header :activename="headerData.name" :savebtn="headerData.btnFlag" @savedata="onSubmit('lineForm')" :title="headerData.title"></inner-header>
      <!--jdy-content-trip end-->
      <el-form class="" :model="lineForm" :rules="rules" ref="lineForm" label-width="100px">
        <el-row>
          <div class="priceTitle"></div>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="lineForm.supplier" placeholder=" "></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="lineForm.supplierTel" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <div class="priceTitle"></div>
        <el-row>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="lineForm.costPrice" placeholder=" "></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="lineForm.factoryPrice" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-date-picker v-model="usefulDate" type="daterange" placeholder="" @change="dateRangeChange"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model="lineForm.remark" type="textarea" autosize></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
  </div>
</template>

<script>
// import lData from './data.js';
import jdyEditor from '@/components/Quilleditor';
import ElFormItem from "../../../../node_modules/element-ui/packages/form/src/form-item";
import ElInputNumber from "../../../../node_modules/element-ui/packages/input-number/src/input-number";
// tab
import innerHeader from './header';
export default {
  name: 'line',
  data() {
    return {
      headerData:{
        name:"ticketPolicyExpandInfo",
        btnFlag:true,
      },
      lineForm:{
        supplier:'',
        supplierTel:'',
        costPrice:'',
        factoryPrice:'',
        lifeStartDate:'',
        lifeEndDate:'',
        remark:'',
        pid:1
      },
      usefulDate:'',
      btnFlag:false,
      rules: {
        tNo: [{ required: true, message: '', trigger: 'blur' },
        { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }],
        tName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 50, message: ' 1  50 ', trigger: 'blur' }
        ],
        pManager: [
          { required: true, message: '', trigger: 'blur' },
        ],
        tType: [
          { required: true, message: '', trigger: 'change', type: "number" }
        ],
        cover: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
    }
  },
  methods: {
    //
    getCost(){
      this.$http.post(api_prefix + '/admission/cost-info',1).then(response => {
        if (response.data.code == 0) {
          let data=response.data.body;
          this.lineForm = data;
          this.usefulDate = [data.lifeStartDate,data.lifeEndDate]
        } else {
          this.$alert(response.data.message)
        }
      }, response => {
        console.log('');
      });
    },
    dateRangeChange(value){
        console.log(this.tickForm.usefulDate,'09090909')
        if (value) {
            let values = value.split(" - ");
            this.lineForm.lifeStartDate = values[0];
            this.lineForm.lifeEndDate = values[1];
        }
    },
    onSubmit(formName, successFun) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
              this.btnFlag = true;
              this.$http.post(api_prefix + '/admission/save-cost', this.lineForm).then(response => {
                if (response.body.code == 0) {
                  this.btnFlag = false;
                  this.$message.success("");
                  successFun && successFun(response.body.id[0]);
                } else {
                  this.btnFlag = false;
                  this.defalutErrorMessage(response.body.message);
                }
              }, response => {
                this.defalutErrorMessage();
              });
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
    defalutErrorMessage(message) { //
      if (message) {
        this.$alert(message, "", { type: "error" });
      } else {
        this.$alert("~", "", { type: "error" });
      }
    },
  },
  components: { 
    ElInputNumber,
    ElFormItem,
    jdyEditor,
    innerHeader
  },
  mounted(){
    this.getCost()
  }
}
</script>
<style lang="less" scoped>
.priceTitle{
  line-height: 30px;
  height: 30px;
  margin: 0 0 15px 10px;
  font-weight: 500;
}
</style>





