 <template>
  <div class="jl-information jl-onlinebill">
    <div class="jdy-content jdy-transfer fleft jl-ad gridmain">
        <div class="ticketLeft">
            left
        </div>
        <div class="ticketRight">
            right
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "information",
  data() {
    return {
      currentPage:1,
      pageSizeAll:20,
      tableDataTotal:0,
      tableData: [
        {
          tNo: 'SC20180305',
          tName: '',
          tType: 1,
          pManager: '',
          tStatus: 1,
          tTime: '2018-03-24 18:24',
        }
      ],
      fullscreenLoading: false, //loadding
      jdytabActive: null,
      ruleForm: {
        tType:'',
        searchName: '',
        pageSize: 20,
        currPage: 1,
      },
      //
      ticketList:[
        {
          id: 1,
          tName: ""
        },
        {
          id: 2,
          tName: ""
        }
      ],
    };
  },
  mounted() {
    // this.getTableData();
  },
  methods: {
    getTableData() {
      //table
      this.$http
        .post(api_prefix + "/SchedulePlan/manageList", this.ruleForm)
        .then(
          response => {
            let data = response.body.body;
            this.tableData = data.pageInfo.list;
            this.tableDataOne = data.pageTotalInfo.list;
            this.tableDataTwo = data.totalInfo.list;
            this.tableDataTotal = data.pageInfo.total;
          },
          response => {
            console.log("");
          }
        );
    },
    jdytab(index) {
      this.jdytabActive = index;
      console.log(index, "index");
      this.getTableData();
    },
    //currentPage 
    handleCurrentChange(val) {
      console.log(val, 'val')
      this.ruleForm.currPage = val;
      this.getTableData()
    },
    firstSearch(){
        this.getTableData();
    },
    //
    refreshBtn() {
      this.fullscreenLoading = true;
      setTimeout(() => {
        this.fullscreenLoading = false;
        this.$message({
          showClose: true,
          message: "",
          type: "success"
        });
        this.getTableData();
      }, 1000);
    },
    //
    declaration(){
      
    },
    addTicket(){
      this.$router.push({ name:'ticketDeclarationInformation'})
    },
    //
    editTicket(){
      this.$router.push({ name:'ticketDeclarationInformation'})
    },
    //
    deleteTicket(index) {
      //
      let url = api_prefix + "/module/delete/" + index;
      this.currentId = index;
      console.log("ID", this.currentId);
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http.get(url).then(
            response => {
              if (response.data.code == 0) {
                this.getTableData();
                setTimeout(() => {
                  this.$message({
                    showClose: true,
                    message: "",
                    type: "success"
                  });
                }, 500);
              } else {
                this.$alert(response.data.message);
              }
            },
            response => {
              console.log("");
            }
          );
        })
        .catch(() => {});
    }
  }
};
</script>
<style scoped lang="less">
.gridmain{
    display: grid;    
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: 900px;
    grid-column-gap:10px;
    // grid-gap: 5px; 
    grid-template-areas: 
        "l l l r r r r r r r";
    .ticketLeft{
        grid-area: l;
    };
    .ticketRight{
        grid-area: r;
    };
}
</style>




