<template>
  <div class="jdy-content jdy-transfer jl-information fleft">
    <div class="jdy-content-inner">
      <inner-header :activename="headerData.name" :savebtn="headerData.btnFlag" :showBtnInTab="headerData.showBtnInTabFlag" :title="headerData.title"></inner-header>
      <!--jdy-content-trip end-->
        <el-button type="primary" @click="dropoff" icon="plus" class="mb10"></el-button>
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column label="" min-width="150px">
              <template scope="scope">
                  <span class="jl-noticeTitle">{{scope.row.apName}}</span>
              </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle" v-if="scope.row.apType == 0"></span>
              <span class="jl-noticeTitle" v-if="scope.row.apType == 1"></span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.workdayCostPrice | moneyTwoPoints}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.workdayFactoryPrice | moneyTwoPoints}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle" v-if="scope.row.apStock != -1">{{scope.row.apStock}}</span>
              <span class="jl-noticeTitle" v-if="scope.row.apStock == -1"></span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="250px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.lifeStartDate}}~{{scope.row.lifeEndDate}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="150px">
            <template scope="scope">
              <span class="jl-noticeTitle">{{scope.row.updateTime | dateFormat('time')}}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="200">
            <template scope="scope">
              <el-button type="primary" @click="editPrice(scope.row.id)" size="mini"></el-button>
              <el-button type="default" @click="deleteTicket(scope.row.id)" size="mini" class="red"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--    begin-->
        <div class="clearfix">
          <el-pagination class="fright pageMargin" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSizeAll" :total="tableDataTotal">
          </el-pagination>
        </div>
        <!-- end-->   
        <!--   begin-->
        <jdy-alert title="" @closeAlert="closeAlert" v-if="refundFlag" class="alertCityList showtfcity refund" style="width:800px;height:700px">
          <div class="produceWrap">
            <el-form :model="produceForm" ref="produceForm" :rules="rules" style="margin-top:10px;" label-width="100px">
              <el-row>
                  <el-col :span="24">
                      <el-form-item label="" prop="apName">
                        <el-input v-model="produceForm.apName" :maxlength="50" placeholder=""></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="apType" placeholder="">
                        <el-select v-model="produceForm.apType" clearable placeholder="" style="width: 100%;">
                          <el-option :key="value.id" v-for="value in ticketList" :label="value.tName" :value="value.id">
                          </el-option>
                        </el-select>
                      </el-form-item>
                      <el-form-item label="" class="priceInfo">
                        <span></span>
                        <el-checkbox v-model="showNormalDay"></el-checkbox>
                        <el-checkbox v-model="showWeekend"></el-checkbox>
                        <el-checkbox v-model="showHolidays"></el-checkbox>
                        <div v-if="showNormalDay">
                          <span class="priceTitle"></span>
                          <span class="priceText redstar"></span><el-input-number :controls="false" :min="0" :max="99999999" v-model="produceForm.workdayCostPrice"></el-input-number>
                          <span class="priceText redstar"></span><el-input-number :controls="false" :min="0" :max="99999999" v-model="produceForm.workdayFactoryPrice"></el-input-number>
                        </div>
                        <div v-if="showWeekend">
                          <span class="priceTitle"></span>
                          <span class="priceText"></span><el-input-number :controls="false" :min="0" :max="99999999" v-model="produceForm.weekendCostPrice"></el-input-number>
                          <span class="priceText"></span><el-input-number :controls="false" :min="0" :max="99999999" v-model="produceForm.weekendFactoryPrice"></el-input-number>
                        </div>
                        <div v-if="showHolidays">
                          <span class="priceTitle"></span>
                          <span class="priceText"></span><el-input-number :controls="false" :min="0" :max="99999999" v-model="produceForm.festivalCostPrice"></el-input-number>
                          <span class="priceText"></span><el-input-number :controls="false" :min="0" :max="99999999" v-model="produceForm.festivalFactoryPrice"></el-input-number>
                        </div>
                      </el-form-item>
                      <el-form-item prop="apStock" label="">
                          <el-input-number v-model="produceForm.apStock" :controls="false" :min="-1" :max="1000000"></el-input-number><span class="stockText">-1</span>
                      </el-form-item>
                      <el-form-item label="">
                          <el-date-picker v-model="usefulDate" type="daterange" placeholder="" @change="dateRangeChange"></el-date-picker>
                      </el-form-item>
                      <el-form-item label="" prop="ticketProductCode">
                        <el-input type="textarea" :rows="3" :autosize="false" :maxlength="120" v-model="produceForm.remark"></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="ticketProductCode">
                        <el-input v-model="produceForm.supplier" :maxlength="30" placeholder=""></el-input>
                      </el-form-item>
                      <el-form-item label="" prop="ticketProductCode">
                        <el-input v-model="produceForm.supplierTel" placeholder=""></el-input>
                      </el-form-item>
                  </el-col>
              </el-row>
            </el-form>
          </div>
          <div class="alertfoot1 clearfix">
            <el-button type="primary" class="fright mt10 mr10" :disabled="btnFlag" @click="savePrice('produceForm')"></el-button>
            <el-button class="fright mt10 mr10" @click="closeAlert"></el-button>
          </div>
        </jdy-alert>
        <!--   end-->
    </div>
  </div>
</template>

<script>
// import lData from './data.js';
import jdyEditor from '@/components/Quilleditor';
import jdyAlert from "@/components/Alert";
import ElFormItem from "../../../../node_modules/element-ui/packages/form/src/form-item";
import ElInputNumber from "../../../../node_modules/element-ui/packages/input-number/src/input-number";
// tab
import innerHeader from './header';
export default {
  name: 'line',
  data() {
    return {
      headerData:{
        name:"ticketDeclarationPrice",
        btnFlag:false,
        title:'',
        showBtnInTabFlag:false
      },
      currentPage:1,
      pageSizeAll:20,
      tableDataTotal:0,
      ruleForm:{
        pid:'',
        pageSize: 20,
        currPage: 1,
      },
      tableData:[

      ],
      produceForm:{
      },
      ticketList:[
        {
          id: 0,
          tName: ""
        },
        {
          id: 1,
          tName: ""
        }
      ],
      showNormalDay:true,
      showWeekend:false,
      showHolidays:false,
      refundFlag:false,
      usefulDate:'',
      btnFlag:false,
      rules: {
        apName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 30, message: ' 1  30 ', trigger: 'blur' }
        ],
        apStock: [
          { required: true, type:"number", message: '', trigger: 'blur' }
        ],
      },
    }
  },
  methods: {
    getTableData(){//
      this.ruleForm.pid = this.$route.query.id;
      this.$http.post(api_prefix + '/admission/price-list',this.ruleForm).then(response => {
        if (response.data.code == 0) {
          this.tableData = response.data.body.resultList;
          this.tableDataTotal = response.data.body.totalNum;
        } else {
          this.$alert(response.data.message)
        }
      }, response => {
        console.log('');
      });
    },
    dateRangeChange(value){
        if (value) {
            let values = value.split(" - ");
            this.produceForm.lifeStartDate = values[0];
            this.produceForm.lifeEndDate = values[1];
        }
    },
    editPrice(id){
      this.refundFlag = true;
      this.$nextTick(function() {
        jdyFn.setAlertStyle("showtfcity");
      });
      this.$http.post(api_prefix + '/admission/price-info',id).then(response => {
        if (response.data.code == 0) {
          let data = response.data.body;
          this.produceForm = response.data.body;
          this.usefulDate = [data.lifeStartDate,data.lifeEndDate]
          let dataValue = data.suitRegion.split("")
          console.log(dataValue,'dataValue')
          if(dataValue[0] == 1){
            this.showNormalDay = true;
          }else{
            this.showNormalDay = false;
          }
           if(dataValue[1] == 1){
            this.showWeekend = true;
          }else{
            this.showWeekend = false;
          }
           if(dataValue[2] == 1){
            this.showHolidays = true;
          }else{
            this.showHolidays = false;
          }
        } else {
          this.$alert(response.data.message)
        }
      }, response => {
        console.log('');
      });
    },
    dropoff(id){
      this.refundFlag = true;
      this.produceForm.apStock = -1;
      this.$nextTick(function() {
        jdyFn.setAlertStyle("showtfcity");
      });
    },
    savePrice(formName){
      this.$refs[formName].validate((valid) => {
        if (valid) {
              this.btnFlag = true;
              //
              let uTel = this.produceForm.supplierTel;
              let rgExps = {
                ExpuContactsPhone: /(?:(\(\+?86\))(0[0-9]{2,3}\-?)?([2-9][0-9]{6,7})+(\-[0-9]{1,4})?)|(?:(86-?)?(0[0-9]{2,3}\-?)?([2-9][0-9]{6,7})+(\-[0-9]{1,4})?)|^(1[3|5|6|8]\d{9})?$/,
              };
              if (!rgExps.ExpuContactsPhone.test(uTel)) {
                this.regexpsAlert("");
                this.btnFlag = false;
                return;
              }
              if(this.produceForm.workdayCostPrice == ''){
                this.$message.error("");
              }else if(this.produceForm.workdayFactoryPrice == ''){
                this.$message.error("");
              }else{
                let dataValue = new Array;
                if(this.showNormalDay == true){
                  dataValue[0] = 1
                }else{
                  dataValue[0] = 0
                }
                if(this.showWeekend == true){
                  dataValue[1] = 1
                }else{
                  dataValue[1] = 0
                }
                if(this.showHolidays == true){
                  this.showHolidays = true;
                  dataValue[2] = 1
                }else{
                  this.showHolidays = false;
                  dataValue[2] = 0
                }
                this.produceForm.suitRegion = dataValue.join("")
                this.produceForm.pid = this.$route.query.id;
                this.$http.post(api_prefix + '/admission/save-price', this.produceForm).then(response => {
                  if (response.body.code == 0) {
                    this.btnFlag = false;
                    setTimeout(() => {
                      this.closeAlert();
                      this.getTableData();
                      this.$message.success("");
                    }, 500);
                    this.produceForm.id = response.body.id[0];
                  } else {
                    this.btnFlag = false;
                    this.defalutErrorMessage(response.body.message);
                  }
                }, response => {
                  this.defalutErrorMessage();
                });
              }
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
    closeAlert() {
      //
      this.refundFlag = false;
      this.produceForm = {};
      this.showWeekend = false;
      this.showHolidays = false;
      this.usefulDate = ''
      $(".alertbgg").remove();
    },
    //
    regexpsAlert(info) {
      this.$message({
        showClose: true,
        message: info,
        type: "error"
      });
    },
    deleteTicket(index) {
      //
      let url = api_prefix + "/admission/delete-price";
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$http.post(url,index).then(
            response => {
              if (response.data.code == 0) {
                this.getTableData();
                setTimeout(() => {
                  this.$message({
                    showClose: true,
                    message: "",
                    type: "success"
                  });
                }, 500);
              } else {
                this.$alert(response.data.message);
              }
            },
            response => {
              console.log("");
            }
          );
        })
        .catch(() => {});
    },
    //
    //currentPage 
    handleCurrentChange(val) {
      console.log(val, 'val')
      this.ruleForm.currPage = val;
      this.getTableData()
    },
    defalutErrorMessage(message) { //
      if (message) {
        this.$alert(message, "", { type: "error" });
      } else {
        this.$alert("~", "", { type: "error" });
      }
    },
  },
  components: { 
    ElInputNumber,
    ElFormItem,
    jdyEditor,
    innerHeader,
    jdyAlert
  },
  mounted(){
    if(this.$route.query.id){
      this.getTableData();
    }
  },
  created(){
    if(this.$route.query.id){
      this.headerData.showBtnInTabFlag = true;
    }
  }
}
</script>
<style lang="less" scoped>
.priceTitle{
  line-height: 30px;
  height: 30px;
  margin: 0 0 15px 10px;
  font-weight: 500;
}
.produceWrap{
  padding: 10px;
  overflow-y: scroll;
  height: 575px;
  .stockText{
    margin-left: 15px;
    color: #bfcbd9
  }
  .priceInfo{
    margin-bottom: 10px;
    .priceText{
      margin-left: 15px;
      display: inline-block;
      width: 70px;
      text-align: right;
    }
    .redstar:before{
      content: '*';
      color: #ff4949;
      margin-right: 4px;
    }
    .el-input-number{
        width: 70px;
        margin: 0 10px;
    }
    .priceTitle{
      display: inline-block;
      width: 50px;
    }
  }
}
</style>





